import { Component, OnInit, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry } from '@angular/material/icon';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { NgbCarousel, NgbSlideEvent, NgbSlideEventSource } from '@ng-bootstrap/ng-bootstrap';

import { SorteioServicesService } from '../../services/sorteio-services.service'
import { Sorteio } from 'src/app/model/sorteio';
import { NumeroSorteioServiceService } from '../../services/numero-sorteio-service.service';
import { Fotos } from 'src/app/model/fotos';
import { NumeroSorteio } from 'src/app/model/numero-sorteio';

@Component({
  selector: 'app-painel-admi',
  templateUrl: './painel-admi.component.html',
  styleUrls: ['./painel-admi.component.css']
})
export class PainelAdmiComponent implements OnInit {
  tabs: Object[] = [{ tab: 'Sorteios' }];
  numeros: NumeroSorteio[] = [];
  selected = new FormControl(0);
  numeroSelecionado: NumeroSorteio;

  paused = false;
  unpauseOnArrow = false;
  pauseOnIndicator = false;
  pauseOnHover = true;
  pauseOnFocus = true;

  @ViewChild('carousel', { static: true }) carousel: NgbCarousel;

  sorteios: any[] = [];
  sorteio: Sorteio;

  idNovoSorteio: any;

  showFiller = true;
  closeResult = '';
  arrayFotos: any[] = [{
    tituloFoto: new FormControl('', Validators.minLength(10)),
    urlFoto: new FormControl('', Validators.minLength(20)),
    boleano: false,
  }]

  formulario: FormGroup;
  tituloSorteio = new FormControl('', Validators.minLength(10))
  valorRifa = new FormControl(10.00, Validators.min(0.01));
  qtdNumeros = new FormControl(null, Validators.min(1));
  descricaoSorteio = new FormControl('', Validators.maxLength(150) && Validators.minLength(20))
  dataSorteio = new FormControl('', Validators.minLength(4));

  constructor(
    iconRegistry: MatIconRegistry,
    sanitizer: DomSanitizer,
    private modalService: NgbModal,
    fb: FormBuilder,
    private sorteioService: SorteioServicesService,
    private numeroSorteioService: NumeroSorteioServiceService,
  ) {
    iconRegistry.addSvgIcon('arrowLeft-svg', sanitizer.bypassSecurityTrustResourceUrl('assets/icons/east-black-18dp.svg'));
    iconRegistry.addSvgIcon('arrowRight-svg', sanitizer.bypassSecurityTrustResourceUrl('assets/icons/west-black-18dp.svg'));
    this.formulario = fb.group({
      valor: this.valorRifa,
      numeros: this.qtdNumeros,
      titulo: this.tituloSorteio,
      descricao: this.descricaoSorteio,
      dataSorteio: this.dataSorteio,
    })
  };

  togglePaused() {
    if (this.paused) {
      this.carousel.cycle();
    } else {
      this.carousel.pause();
    }
    this.paused = !this.paused;
  }

  onSlide(slideEvent: NgbSlideEvent) {
    if (this.unpauseOnArrow && slideEvent.paused &&
      (slideEvent.source === NgbSlideEventSource.ARROW_LEFT || slideEvent.source === NgbSlideEventSource.ARROW_RIGHT)) {
      this.togglePaused();
    }
    if (this.pauseOnIndicator && !slideEvent.paused && slideEvent.source === NgbSlideEventSource.INDICATOR) {
      this.togglePaused();
    }
  }

  listarNumeros(sorteio: Sorteio) {
    this.numeroSorteioService.get(sorteio.id).subscribe(resp => {
      this.numeros = [];
      resp.forEach(r => {
        this.numeros.push({
          id: r.payload.doc.id,
          ...r.payload.doc.data() as NumeroSorteio,
        })
      })
      this.tabs[this.selected.value]['numeros'] = this.numeros.sort((a, b) => {
        if (a.numero > b.numero) {
          return 1
        }
        if (a.numero < b.numero) {
          return -1
        }
        return 0
      })
    })
  }

  addTab(selectAfterAdding: boolean, sorteio?: Sorteio) {
    this.tabs.push({
      tab: sorteio.tituloSorteio,
      ...sorteio,
      numeros: [],
    });
    this.listarNumeros(sorteio);
    if (selectAfterAdding) {
      this.selected.setValue(this.tabs.length - 1);
    }
  };

  removeTab(index: number) {
    this.tabs.splice(index, 1);
  }

  open(content) {
    this.modalService.open(content, { size: 'lg', ariaLabelledBy: 'modal-basic-title' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  openModalNumero(content, numero?: NumeroSorteio) {
    this.numeroSelecionado = numero;
    this.modalService.open(content, { size: 'sm' });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  mascaraValor(valor: number) {
    return Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(valor)
  }

  mascaraData(valor) {
    let data = new Date(valor);
    let dia = data.getDate().toString().padStart(2, '0');
    let mes = (data.getMonth() + 1).toString().padStart(2, '0');
    let ano = data.getFullYear();
    return dia + "/" + mes + "/" + ano;
  }

  addMaisFoto() {
    this.arrayFotos.push({
      tituloFoto: new FormControl('', Validators.minLength(10)),
      urlFoto: new FormControl('', Validators.minLength(10)),
      boleano: true,
    })

  };

  dltFoto(index: number) {
    this.arrayFotos.splice(index, 1)
  }

  concluir(form: FormGroup) {
    let fotos: any[] = [];
    let sorteio: Sorteio;

    this.arrayFotos.forEach(foto => {
      fotos.push({
        nomeFoto: foto.tituloFoto.value,
        urlFoto: foto.urlFoto.value,
      })
    })

    sorteio = {
      dataSorteio: form.value.dataSorteio,
      descricao: form.value.descricao,
      qtdNumeros: form.value.numeros,
      valorRifa: form.value.valor,
      tituloSorteio: form.value.titulo,
      arrayFotos: fotos,
      ativo: true,
    } as Sorteio;
    this.criarSorteio(sorteio);
  }

  consultarSorteio() {
    this.sorteioService.get().subscribe(data => {
      this.sorteios = [];
      data.forEach(sort => {
        this.sorteios.push({
          id: sort.payload.doc.id,
          ...sort.payload.doc.data() as Object,
        })
      })
    });
  }

  adicionarZero(numero: number) {
    if (numero < 10) {
      return "00" + numero;
    } else if (numero < 100) {
      return "0" + numero;
    }
    return "" + numero;
  }

  criarSorteio(sort: Sorteio) {
    let arrayNumeros: NumeroSorteio[] = [];

    this.sorteioService.post(sort).then(sorteio => {
      if (sorteio.id != '') {
        for (let i = 0; i < sort.qtdNumeros; i++) {
          arrayNumeros.push({
            numero: this.adicionarZero(i),
            status: 'disponivel',
            comprador: '',
            telefone: '',
          } as NumeroSorteio);
        }
      }
      this.formulario.reset();
      this.arrayFotos = [{
        tituloFoto: new FormControl('', Validators.minLength(10)),
        urlFoto: new FormControl('', Validators.minLength(20)),
        boleano: false,
      }];
      this.criarNumeros(sorteio.id, arrayNumeros);
    })
  }

  deletarSorteio(sorteio: any) {
    this.sorteioService.delete(sorteio.id).then(resp => {
      alert('Sorteio do id ' + sorteio.tituloSorteio + ' deletado com sucesso')
    })
  };

  criarNumeros(idSorteio: string, numeros: NumeroSorteio[]) {
    let body = {
      idSorteio: idSorteio,
      listaNumeros: numeros as NumeroSorteio[],
    }
    this.numeroSorteioService.insert(body.idSorteio, numeros)
  };

  pesquisar(text: string, aba: string) {
    let numerosFiltros: NumeroSorteio[] = [];

    this.numeros.forEach(num => {
      if (num.numero.toLowerCase().includes(text.toLowerCase()) || num.status.toLowerCase().includes(text.toLowerCase()) || num.comprador.toLowerCase().includes(text.toLowerCase()) || num.telefone.toLowerCase().includes(text.toLowerCase()))
        numerosFiltros.push(num)
    })
    // console.log(aba)
    let indexAba = this.tabs.length;
    this.tabs[this.selected.value]['numeros'] = numerosFiltros;
  }

  filtrarQtdStatus(status: string) {
    let qtdNumeros: number = 0;
    this.tabs[this.selected.value]['numeros'].forEach(num => {
      if (num.status.includes(status)) {
        qtdNumeros++;
      }
    })
    return qtdNumeros;
  }

  confirmarPag() {
    this.numeroSelecionado.status = "pago";
    this.numeroSorteioService.update(this.tabs[this.selected.value]['id'], this.numeroSelecionado)
  }

  numeroNaoPago() {
    this.numeroSelecionado.status = "disponivel";
    this.numeroSorteioService.update(this.tabs[this.selected.value]['id'], this.numeroSelecionado)
  }

  ngOnInit(): void {
    this.consultarSorteio();
  }

}
