import { Component, OnInit } from '@angular/core';
import { NumeroSorteio } from "../../model/numero-sorteio";
import { Status } from 'src/app/model/status.enum';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';
import { SorteioServicesService } from 'src/app/services/sorteio-services.service';
import { Sorteio } from 'src/app/model/sorteio';
import { NumeroSorteioServiceService } from 'src/app/services/numero-sorteio-service.service';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

declare var $: any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  sorteio: Sorteio;
  numerosSorteio: NumeroSorteio[];
  numeroSelecionado: NumeroSorteio;

  closeResult = '';
  formReservarNr: FormGroup;
  ano: number = new Date().getFullYear();

  constructor(private modalService: NgbModal,
    private fb: FormBuilder,
    sanitizer: DomSanitizer,
    config: NgbCarouselConfig,
    private sorteioService: SorteioServicesService,
    private numerosSortService: NumeroSorteioServiceService,
    iconRegistry: MatIconRegistry,
  ) {
    config.interval = 10000;
    config.wrap = false;
    config.keyboard = false;
    config.pauseOnHover = false;
    iconRegistry.addSvgIcon('instagran-svg', sanitizer.bypassSecurityTrustResourceUrl('assets/icons/instagram-sketched.svg'))
  }

  private createForm(): FormGroup {
    return this.fb.group({
      numero: new FormControl(""),
      comprador: new FormControl("",
        Validators.compose([Validators.required,
        Validators.pattern("[a-zA-Z]+"),
        Validators.minLength(3),
        Validators.maxLength(100)
        ])
      ),
      telefone: new FormControl("", Validators.compose([
        Validators.required, Validators.pattern("\\d+"),
        Validators.minLength(11),
        Validators.maxLength(11)
      ]))
    });
  }

  listarSorteioAtivo() {
    let listaSorteio: Sorteio[] = [];
    this.sorteioService.get().subscribe(respose => {
      respose.forEach(resp => {
        listaSorteio.push({
          id: resp.payload.doc.id,
          ...resp.payload.doc.data() as Sorteio
        })
      })
      this.sorteio = listaSorteio.filter(filt => filt.ativo)[0];
      this.listarNumerosSorteio(this.sorteio.id)
    });
  }

  listarNumerosSorteio(idSorteio: string) {
    this.numerosSortService.get(idSorteio).subscribe(respose => {
      this.numerosSorteio = [];
      respose.forEach(resp => {
        this.numerosSorteio.push({
          id: resp.payload.doc.id,
          ...resp.payload.doc.data() as NumeroSorteio
        })
      })
      this.sorteio.numeros = this.numerosSorteio.sort((a, b) =>  {
        if (a.numero > b.numero) {
          return 1
        }
        if (a.numero < b.numero) {
          return -1
        }
        return 0
      });
    })
  };

  reservaNumero(modal) {
    this.numeroSelecionado.comprador = modal.value.comprador;
    this.numeroSelecionado.telefone = modal.value.telefone;
    this.numeroSelecionado.status = "reservado";
    this.numerosSortService.update(this.sorteio.id, this.numeroSelecionado);
    this.numeroSelecionado = null;
    this.formReservarNr.reset()
  }

  ngOnInit(): void {
    this.listarSorteioAtivo()
  }

  open(content, numero?: NumeroSorteio) {
    this.numeroSelecionado = numero;
    this.formReservarNr = this.createForm();
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  enviarForm(modal: any) {
    modal.close('Save click');
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  mascaraValor(valor: number) {
    return Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(valor)
  }

  filtrarStatus(status: string) {
    let contador: number = 0;
    this.numerosSorteio.forEach(sort => {
      if (sort.status.includes(status)) {
        contador++
      }
    })
    return contador;
  }

  mascaraNumeros(num: number) {
    if(num < 10) {
      return "00"+num;
    }
    if(num < 100) {
      return "0"+num;
    }
    return ""+num;
  }
}