import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { Sorteio } from "../model/sorteio"
import { NumeroSorteio } from '../model/numero-sorteio';

@Injectable({
  providedIn: 'root'
})
export class SorteioServicesService {

  constructor(private firestore: AngularFirestore) { }

  get() {
    return this.firestore.collection('sorteios').snapshotChanges();
  }

  post(sorteio: Sorteio) {
    return this.firestore.collection('sorteios').add(sorteio);
  }

  delete(idSorteio: string) {
    return this.firestore.doc('sorteios/' + idSorteio).delete();
  }

}
